.projectDetails {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	margin: auto;
	display: flex;
	align-items: flex-end;
	justify-content: center;
	animation: darken 1s ease;
	background: rgba(0, 0, 0, 0.8);
}

.projectDetails-content {
	background: white;
	color: black;
	padding: 50px;
	width: 80%;
	max-width: 1400px;
	box-sizing: border-box;
	font-size: 30px;
	animation: slide-up 1s ease;
	height: 98%;
	overflow: scroll;
	border-radius: 10px 10px 0px 0px;
}

.close-btn {
	float: right;
}

@keyframes slide-up {
	from {
		margin-bottom: -150vh;
	}
	to {
		margin-bottom: 0px;
	}
}
@keyframes darken {
	from {
		background: none;
	}
	to {
		background: rgba(0, 0, 0, 0.8);
	}
}
